<template>
  <mu-flex justify-content="center">
    <mu-card raised class="card-main">
      <mu-flex wrap="wrap" v-bind:justify-content="isPc?'between':'center'" v-if="userInfo">
        <mu-flex fill>
          <!--  -->
          <mu-card v-loading="loading1" class="card-info" style="">
            <mu-card-title class="title-name" :title="userInfo.name">
            </mu-card-title>
            <mu-list dense>
              <!-- <mu-list-item>
                {{userInfo.score}}
              </mu-list-item> -->
              <mu-list-item v-if="userInfo.qq">
                QQ{{userInfo.qq}}
              </mu-list-item>
              <mu-list-item v-if="userInfo.tel">
                TEL{{userInfo.tel}}
              </mu-list-item>
            </mu-list>
            <mu-list v-if="userInfo.team.id">

              <mu-sub-header></mu-sub-header>
              <mu-list-item avatar button :ripple="true">
                <mu-list-item-content>
                  <mu-list-item-title>{{userInfo.team.name}}</mu-list-item-title>
                  <mu-list-item-sub-title><span style="margin-right:10px"> ID{{userInfo.team.id}}</span></mu-list-item-sub-title>
                    <!-- {{userInfo.team.score}} -->
                </mu-list-item-content>

                <!-- <mu-list-item-action v-if="userInfo.team.score==0"> -->

                  <!-- <mu-tooltip v-if="userInfo.team.leader != userInfo.name" placement="top" content="">
                    <mu-button icon color="blue" @click="quitTeam(userInfo.id,false)">
                      <mu-icon value="reply"></mu-icon>
                    </mu-button>
                  </mu-tooltip> -->

                  <!-- <mu-tooltip v-else placement="top" content="">
                    <mu-button icon color="red" @click="openDel=true">
                      <mu-icon value="delete"></mu-icon>
                    </mu-button>
                  </mu-tooltip>

                  <mu-dialog title="" width="400px" max-width="80%" :esc-press-close="false" :overlay-close="false"
                    :open.sync="openDel">
                    
                    <mu-button slot="actions" @click="openDel = false"></mu-button>
                    <mu-button slot="actions" v-loading="loading2" data-mu-loading-size="24" :disabled="loading2" color="red"
                      @click="delTeam"> </mu-button>
                  </mu-dialog> -->

                <!-- </mu-list-item-action> -->
              </mu-list-item>

              <mu-list-item avatar :ripple="true" button>
                <mu-list-item-action class="avatar-member">
                  <mu-icon value="person"></mu-icon>
                </mu-list-item-action>
                <mu-list-item-content>
                  <mu-list-item-title>{{userInfo.team.leader}}</mu-list-item-title>
                </mu-list-item-content>
                <mu-list-item-action>
                  <mu-badge content="leader" color="secondary"></mu-badge>
                </mu-list-item-action>
              </mu-list-item>

              <mu-list-item v-for="mem in userInfo.team.mems" v-if="mem.name!=userInfo.team.leader" :key="mem.id"
                avatar :ripple="true" button>
                <mu-list-item-action class="avatar-member">
                  <mu-icon value="person"></mu-icon>
                </mu-list-item-action>
                <mu-list-item-content>
                  <mu-list-item-title>{{mem.name}}</mu-list-item-title>
                </mu-list-item-content>
                <!-- <mu-list-item-action v-if="userInfo.team.leader == userInfo.name && mem.name !=userInfo.name && userInfo.team.score==0">
                  <mu-tooltip placement="top" content="">
                    <mu-button @click="quitTeam(mem.id,true)" icon color="error">
                      <mu-icon value="close"></mu-icon>
                    </mu-button>
                  </mu-tooltip>
                </mu-list-item-action> -->
              </mu-list-item>

            </mu-list>
          </mu-card>
        </mu-flex>
        <mu-flex fill>
          <div class="demo-vsteper-container">
            <mu-stepper :active-step="vactiveStep" orientation="vertical">
              <!-- step1  -->
              <mu-step>
                <mu-step-label>
                  
                </mu-step-label>
                <mu-step-content>
                  <p>
                    <span v-if="!userInfo.tel">~</span>
                    <span v-else>~</span>
                  </p>
                  <mu-button class="demo-step-button" @click="openRegister = true;
                          validateForm.tel=userInfo.tel;
                          validateForm.qq=userInfo.qq"
                    color="primary">
                    <span v-if="!userInfo.tel"></span>
                    <span v-else></span>
                  </mu-button>
                  <mu-button v-if="userInfo.tel" flat class="demo-step-button" @click="vhandleNext"></mu-button>

                  <!-- <mu-dialog :title="userInfo.tel?'':''" width="400px" max-width="80%" :esc-press-close="false"
                    :overlay-close="false" :open.sync="openRegister"> -->
                     <mu-dialog :title="userInfo.tel?'':''" width="400px" max-width="80%" :esc-press-close="false"
                    :overlay-close="false" :open.sync="openRegister">
                    <!-- <mu-form ref="form" :model="validateForm" class="mu-demo-form">
                      <mu-form-item label-float label="" help-text="" prop="tel" :rules="telRules">
                        <mu-text-field type="number" v-model="validateForm.tel" prop="tel"></mu-text-field>
                      </mu-form-item>
                      <mu-form-item class="lastone" label-float label="QQ" prop="qq" :rules="qqRules">
                        <mu-text-field type="number" v-model="validateForm.qq" prop="qq"></mu-text-field>
                      </mu-form-item>
                    </mu-form> -->
                    <p>
                      <span v-if="!userInfo.tel"></span>
                      <span v-else></span>
                    </p>
                    <mu-button slot="actions" @click="openRegister = false"></mu-button>
                    <!-- <mu-button v-loading="loading4" data-mu-loading-size="24" :disabled="loading4" slot="actions" color="success" @click="submitEnroll">
                      <span v-if="!userInfo.tel"></span>
                      <span v-else></span>
                    </mu-button> -->
                  </mu-dialog>

                </mu-step-content>
              </mu-step>
              <!-- step2  -->
              <mu-step v-if="!userInfo.team.id">
                <mu-step-label>
                  /
                </mu-step-label>
                <mu-step-content>
                  <p>
                    <strong></strong><strong></strong>~<br>
                    Ps.3~
                  </p>
                  <mu-button class="demo-step-button" @click="openJoinTeam = true" color="primary"></mu-button>
                  <mu-button class="demo-step-button" @click="openNewTeam = true;" color="primary"></mu-button>

                  <mu-dialog title="" width="400px" max-width="80%" :esc-press-close="false" :overlay-close="false"
                    :open.sync="openNewTeam">
                    <mu-form ref="form" :model="newTeamForm" class="mu-demo-form">
                      <mu-form-item label-float label="" help-text="" prop="teamname" :rules="teamnameRules">
                        <mu-text-field type="text" v-model="newTeamForm.teamname" prop="teamname"></mu-text-field>
                      </mu-form-item>
                    </mu-form>
                    <mu-button slot="actions" @click="openNewTeam = false"></mu-button>
                    <mu-button v-loading="loading5" data-mu-loading-size="24" :disabled="loading5" slot="actions" color="success"
                      @click="submitCreate"></mu-button>
                  </mu-dialog>

                  <mu-dialog title="" width="400px" max-width="80%" :esc-press-close="false" :overlay-close="false"
                    :open.sync="openJoinTeam">
                    <mu-form ref="form" :model="teamFindForm" class="mu-demo-form">
                      <mu-form-item label-float label="id//" help-text="id" prop="teamfind" :rules="teamfindRules">
                        <mu-text-field type="text" v-model="teamFindForm.teamfind" prop="teamfind"></mu-text-field>
                      </mu-form-item>
                      <span v-if="teamFound.name" style="color:#000;padding-right:10px"> <strong>{{teamFound.name}}</strong>
                      </span> <span v-if="teamFound.id"> id:{{teamFound.id}} {{teamFound.leader}}</span>
                    </mu-form>
                    <mu-button slot="actions" @click="openJoinTeam = false"></mu-button>
                    <mu-button v-loading="loading5" data-mu-loading-size="24" :disabled="loading5 && !teamFound.id"
                      slot="actions" color="success" @click="submitJoin"></mu-button>
                  </mu-dialog>

                  <mu-button flat class="demo-step-button" @click="vhandlePrev" disabled></mu-button>
                </mu-step-content>
              </mu-step>
              <!-- step2  -->
              <mu-step v-if="userInfo.team.id && userInfo.team.mems.length<=3">
                <mu-step-label>
                  
                </mu-step-label>
                <mu-step-content>
                  <p>
                    ~
                    <br>
                    <span style="color:red">
                      <strong> 
                        ~
                      </strong>
                    </span>
                  </p>
                  <mu-button class="demo-step-button" @click="openShare=true;" color="primary"></mu-button>

                  <mu-dialog title="" width="360" :open.sync="openShare">
                    ID{{userInfo.team.id}}<br>
                    {{userInfo.team.name}}<br>
                    {{userInfo.team.leader}}<br>
                    <!-- <mu-button @click="qrcode" color="primary"></mu-button>
                    <div id="qrcode" >
                    </div> -->
                    <mu-button slot="actions" flat color="primary" @click="openShare=false"></mu-button>
                  </mu-dialog>

                  <mu-button flat class="demo-step-button" @click="vhandlePrev" disabled></mu-button>
                </mu-step-content>
              </mu-step>
              <!-- step3  -->
              <mu-step>
                <mu-step-label>
                  
                </mu-step-label>
                <mu-step-content>
                  <strong style="color:red"></strong> 
                </mu-step-content>
              </mu-step>

              <mu-step>
                <mu-step-label>
                  
                </mu-step-label>
                <mu-step-content>
                  <p>
                    1.179<br>
                    2.50
                  </p>
                  <mu-button class="demo-step-button" @click="goAnswer" color="primary" :disabled="userInfo.isFinished">{{!userInfo.isFinished?'':''}}</mu-button>
                </mu-step-content>
              </mu-step>
              <mu-step>
                <mu-step-label>
                  
                </mu-step-label>
                <mu-step-content>
                  <p>
                    1. <br>
                    2.10220<br>
                    3.15
                  </p>
                  <mu-button class="demo-step-button" @click="goAnswer" color="primary"></mu-button>
                </mu-step-content>
              </mu-step>

              <mu-step>
                <mu-step-label>
                  
                </mu-step-label>
                <mu-step-content>
                  <p>
                    1.<br />
                    2.10220<br />
                    3.15<br>
                  </p>
                  <mu-button class="demo-step-button" @click="goAnswer" color="primary"></mu-button>
                </mu-step-content>
              </mu-step>

              <mu-step>
                <mu-step-label>
                  
                </mu-step-label>
                <mu-step-content>
                  <p>
                    1.7515130y=2^n n=012<br>
                    2.
                  </p>
                  <mu-button class="demo-step-button" @click="goAnswer" color="primary"></mu-button>
                </mu-step-content>
              </mu-step>

            </mu-stepper>
          </div>
        </mu-flex>
      </mu-flex>
      <mu-flex v-else>
        <h1></h1>
      </mu-flex>
    </mu-card>
  </mu-flex>
</template>
<script>
  // import QRCode from "qrcodejs2";
  var debounce = require("lodash.debounce");
  export default {
    data() {
      return {
        vactiveStep: 1,
        isTeamed: true,
        fullWidth: document.documentElement.clientWidth,
        isPc: document.documentElement.clientWidth > 900,
        userInfo,
        openRegister: false,
        openNewTeam: false,
        openJoinTeam: false,
        openShare: false,
        openDel: false,
        teamFound: {},

        loading1: true,
        loading2: false,
        loading3: false,
        loading4: false,
        loading5: false,

        telRules: [{
            validate: val => !!val,
            message: ""
          },
          {
            validate: val => /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/.test(val),
            message: ""
          }
        ],
        qqRules: [{
            validate: val => !!val,
            message: "qq"
          },
          {
            validate: val => val.length >= 6 && val.length <= 12,
            message: "qq"
          }
        ],
        teamnameRules: [{
            validate: val => !!val,
            message: ""
          },
          {
            validate: val => val.length >= 3 && val.length <= 10,
            message: "3-10"
          },
          {
            validate: val => !/[`~@#$%^&*()+<>:"{},.\/;'#|[\]]/.test(val),
            message: ""
          }
        ],
        teamfindRules: [{
            validate: val => !!val,
            message: ""
          },
          {
            validate: val => !/[`~@#$%^&*()+<>:"{},.\/;'#|[\]]/.test(val),
            message: ""
          }
        ],
        validateForm: {
          tel: "",
          qq: ""
        },
        newTeamForm: {
          teamname: ""
        },
        teamFindForm: {
          teamfind: ""
        }
      };
    },
    computed: {
      vfinished() {
        return this.vactiveStep > 4;
      }
    },
    watch: {
      "teamFindForm.teamfind": function (newTeam, oldTeam) {
        if (newTeam.length == 0) {
          this.teamFound = {};
          return;
        }
        this.debouncedGetTeam();
      }
    },
    methods: {
      goAnswer() {
        this.$router.push("Answer");
      },
      vhandleNext() {
        this.vactiveStep++;
      },
      vhandlePrev() {
        this.vactiveStep--;
      },
      vreset() {
        this.vactiveStep = 0;
      },
      submitEnroll() {
        //
        this.$refs.form.validate().then(result => {
          // console.log("form valid: ", result);
          if (result) {
            this.loading4 = true;
            this.$axios
              .post(this.url + "register", this.validateForm)
              .then(res => {
                // console.log(res);
                if (res.data.isOk) {
                  this.openRegister = false;
                  this.vhandleNext();
                  this.show_toast("", 0);
                  this.userInfo = res.data.userInfo;
                  this.loading4 = false;
                  refreshStep();
                } else {
                  this.show_toast(res.data.errmsg, 1);
                  this.loading4 = false;
                }
              })
              .catch(res => {
                // console.log(res);
                this.show_toast("", 1);
                this.loading4 = false;
              });
          }
        });
      },
      submitCreate() {
        //
        this.$refs.form.validate().then(result => {
          if (result) {
            this.loading5 = true;
            this.$axios
              .post(this.url + "new_team", this.newTeamForm)
              .then(res => {
                // console.log(res);
                if (res.data.isOk) {
                  this.openNewTeam = false;
                  // this.vhandleNext();
                  this.show_toast("", 0);
                  this.userInfo = res.data.userInfo;
                } else {
                  this.show_toast(res.data.errmsg, 1);
                }
                this.refreshStep();
                this.loading5 = false;
              })
              .catch(res => {
                // console.log(res);
                this.show_toast("", 1);
                this.loading5 = false;
              });

            console.log(this.newTeamForm);
          }
        });
      },
      submitJoin() {
        //
        if (!this.teamFound.id) {
          return;
        }
        this.loading5 = true;
        this.$axios
          .post(this.url + "join_team", this.teamFound)
          .then(res => {
            console.log(res);
            if (res.data.isOk) {
              this.openJoinTeam = false;
              // this.vhandleNext();
              this.show_toast("", 0);
              this.userInfo = res.data.userInfo;
            } else {
              this.show_toast(res.data.errmsg, 1);
            }
            this.refreshStep();
            this.loading5 = false;
          })
          .catch(res => {
            console.log(res);
            this.show_toast("", 1);
            this.loading5 = false;
          });
      },

      shareTeam() {
        //
      },
      getTeam() {
        this.teamFound = {
          name: ""
        };
        var that = this;
        if (this.teamFindForm.teamfind.length != 0) {
          this.$axios
            .post(this.url + "find_team", this.teamFindForm)
            .then(res => {
              console.log(res);
              if (res.data.isOk) {
                this.teamFound = res.data.team;
              } else {
                this.teamFound = {
                  name: ""
                };
              }
            })
            .catch();
        } else {
          this.teamFound = {};
        }
      },
      quitTeam(memid, isLeader) {
        this.$axios

          .post(this.url + "quit_team", {
            id: memid
          })
          .then(res => {
            console.log(res);
            if (!isLeader) {
              if (res.data.isOk) {
                this.show_toast("", 0);
                this.userInfo = res.data.userInfo;
              } else {
                this.show_toast(res.data.errmsg, 1);
              }
            } else {
              if (res.data.isOk) {
                this.show_toast("", 0);
                this.userInfo = res.data.userInfo;
              } else {
                this.show_toast(res.data.errmsg, 1);
              }
            }
            this.refreshStep();
          })
          .catch(res => {
            console.log(res);
            this.show_toast("", 1);
          });
      },
      delTeam() {
        this.loading2 = true;
        this.$axios
          .post(this.url + "del_team")
          .then(res => {
            console.log(res);
            if (res.data.isOk) {
              this.show_toast("", 0);
              this.userInfo = res.data.userInfo;
              this.openDel = false;
            } else {
              this.show_toast(res.data.errmsg, 1);
            }
            this.loading2 = false;
            this.refreshStep();
          })
          .catch(res => {
            console.log(res);
            this.show_toast("", 1);
            this.loading2 = false;
          });
      },

      show_toast(string, type) {
        // console.log(string)
        if (type == 1) {
          this.$toast.error(string);
        } else {
          this.$toast.success(string);
        }
      },
      qrcode() {
        let qrcode = new QRCode("qrcode", {
          width: 200,
          height: 200, // 
          text: this.url // 
          // render: 'canvas'
          //  tablecanvascanvas
          // background: '#eeeeee',
          // foreground: '#FF00FF'
        });
        console.log(qrcode);
      },
      refreshStep() {
        if (!this.userInfo.tel) {
          this.vactiveStep = 0;
        } else if (this.userInfo.team.mems) {
          if (this.userInfo.team.mems.length >= 3) {
            this.vactiveStep = 1;
            this.vactiveStep += this.userInfo.stage;
          }
        } else {
          this.vactiveStep = 1;
        }
      }
    },
    created() {
      const that = this;
      this.debouncedGetTeam = debounce(this.getTeam, 500);
      if (!localStorage.getItem("isLogin")) {
        console.log("jumpToLogin");
        this.$router.push("Login");
        return;
      }
      window.onresize = () => {
        return (() => {
          window.fullHeight = document.documentElement.clientHeight;
          that.fullHeight = window.fullHeight;
          that.isPc = that.fullWidth > 1000;
        })();
      };
      //
      this.$axios
        .post(this.url + "get_team")
        .then(res => {
          console.log(res);
          if (res.data.isOk) {
            console.log("");
            this.userInfo = res.data.userInfo;
            this.openDel = false;
            //step
            this.refreshStep();
            this.loading1 = false;
          } else {
            console.log("");
            this.show_toast(res.data.errmsg, 1);
            localStorage.setItem("isLogin", "");
            this.$router.push("Login");
            this.loading1 = false;
          }
        })
        .catch(res => {
          console.log(res);
          this.show_toast("", 1);
          this.loading1 = false;
        });
    }
  };

</script>

<style>
  .demo-vsteper-container {
    max-width: 24rem;
    max-height: 100rem;
    margin: auto;
    margin-left: 2.5rem;
  }

  .demo-step-content {
    margin: 0 16px;
  }

  .demo-step-button {
    margin-top: 12px;
    margin-right: 12px;
  }

  .msg {
    padding: 1rem;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }

  .title-name {
    padding-bottom: 0;
  }

  .avatar-member {
    /* padding-left: 3rem; */
    /* padding-right: 0.5rem; */
    min-width: 40px;
  }

  .mu-demo-form {
    padding-bottom: 0;
  }

  .lastone {
    margin-bottom: 0;
  }

  /* .mu-dialog-body{
    padding-bottom: 0;
} */
  /*  */
  .mu-dialog-actions {
    padding-bottom: 15px;
    padding-right: 15px;
  }

  @media screen and (max-width: 1200px) {
    .mu-tab {
      min-width: 100px;
    }
  }

  @media screen and (min-width: 500px) {
    .card-info {
      margin-left: 2rem;
      margin-top: 1rem;
      width: 100%;
      max-width: 375px;
      min-width: 250px;
    }

    .card-main {
      margin: 2rem 0;
      padding: 2rem 1rem;
    }
  }

  @media screen and (max-width: 500px) {
    .demo-vsteper-container {
      margin: auto;
      margin-top: 2rem;
    }

    .card-info {
      width: 100%;
      max-width: 450px;
    }

    .card-main {
      margin: 1rem 0;
      padding: 2rem 2rem;
    }
  }

</style>



