<template>
    <div>
        <mu-card class="title-card">
            <mu-card-title title="Just" sub-title=""></mu-card-title>
        </mu-card>

        <mu-expansion-panel>
            <div slot="header"> 10.1-10.15</div>
            <br/>
        </mu-expansion-panel>
        <mu-expansion-panel>
            <div slot="header"> 10.12-10.16</div>
            1.179<br/>
